import React from "react";

const synth = window.speechSynthesis;
const utterThis = new window.SpeechSynthesisUtterance("");

const cancel = () => synth.cancel;
const pause = () => synth.pause;
const resume = () => synth.resume;

let voices;
let defaultVoice;
let currentVoice;
const setupVoices = () => {
  voices = window.speechSynthesis.getVoices();
  defaultVoice = voices.find(({ 'default': isDefault }) => isDefault);
  currentVoice = defaultVoice;
  window.speechSynthesis.removeEventListener('onvoiceschanged', setupVoices);
}
window.speechSynthesis.addEventListener('onvoiceschanged', setupVoices);
setupVoices();

let _setVoice = () => {};
const VoicePicker = () => {
  const [voice, setVoice] = React.useState(currentVoice);
  _setVoice = setVoice;

  return props.children({
    voice,
    voices,
    setVoice,
  });
}

const updateVoiceByVoiceURI = voiceURI => {
  const found = voices.find(({ _voiceURI }) => _voiceURI === voiceURI);
  if (found) {
    currentVoice = found;
  }
  _setVoice(found);

  return !!found;
}

const speak = text => {
  utterThis.text = text;
  synth.speak(utterThis);
};

const SpeachSythesisRenderer = props =>
  props.children({
    ...props,
    speak,
    cancel,
    pause,
    resume,
  });

export default speak;
export {
  updateVoiceByVoiceURI,
  VoicePicker,
  SpeachSythesisRenderer,
  speak,
};
