export default {
    list: [
    { a: "aktor", q: "Uytkownik lub dowolna inna osoba lub system, ktry wchodzi w okrelony sposb w interakcj z testowanym systemem."},
    { a: "analiza ryzyka", q: "Proces oceny zidentyfikowanych ryzyk majcy na celu oszacowanie ich wpywu i prawdopodobiestwa ziszczenia si."},
    { a: "analiza statyczna", q: "Analiza artefaktw oprogramowania, np. wymaga bd kodu programu przeprowadzona bez wykonywania tych artefaktw, Analiza ****** jest na og przeprowadzana przy pomocy narzdzi"},
    { a: "analiza wartoci brzegowych", q: "Czarnoskrzynkowa technika projektowania przypadkw testowych, w ktrej przypadki te s projektowane w oparciu o wartoci brzegowe."},
    { a: "analiza wpywu", q: "Oszacowanie zmiany w dokumentacji projektowej, testowej oraz zmian w moduach koniecznych do implementacji danej zmiany wg okrelonych wymaga."},
    { a: "analizator statyczny kodu", q: "Narzdzie wykonujce analiz statyczn kodu. Narzdzie takie sprawdza kod rdowy pod wzgldem pewnych waciwoci, takich jak zgodno ze standardami kodowania, metryki jakoci lub anomalie przepywu danych."},
    { a: "atak", q: "Ukierunkowane dziaanie majce na celu ocen jakoci, w szczeglnoci niezawodnoci obiektu testw, poprzez wymuszanie wystpienia okrelonej awarii."},
    { a: "atrybut jakociowy", q: "Cecha lub waciwo, ktra wpywa na jako obiektu."},
    { a: "automatyzacja testowania", q: "Uycie oprogramowania do wykonania lub wspierania czynnoci testowych, np. zarzdzania testami, projektowania testw, wykonania testw i sprawdzania wynikw."},
    { a: "awaria", q: "Odchyka moduu lub systemu od oczekiwanego zachowania lub rezultatu dziaania."},
    { a: "bezpieczestwo", q: "Zdolno oprogramowania do osigania akceptowalnych poziomw ryzyka wystpienia szkody w stosunku do ludzi, biznesu, oprogramowania, majtku lub rodowiska w okrelonym kontekcie uycia. "},
    { a: "biaoskrzynkowe techniki projektowania testw", q: "Procedura tworzenia i/ lub wybierania przypadkw testowych oparta na analizie wewntrznej struktury moduu lub systemu."},
    { a: "bd", q: "Dziaanie czowieka powodujce powstanie nieprawidowego rezultatu."},
    { a: "cel testu", q: "Przyczyna lub powd zaprojektowania i przeprowadzenia testu"},
    { a: "certyfikacja", q: "Proces potwierdzajcy, e modu, system lub osoba spenia okrelone wymagania. Certyfikat mona uzyska np. poprzez zdanie egzaminu."},
    { a: "COTS", q: "Akronim od ang. Commercial Off-The-Shelf Software. Patrz oprogramowanie z pki."},
    { a: "cykl ycia oprogramowania", q: "Okres czasu rozpoczynajcy si, kiedy pojawi si pomys na oprogramowanie i koczcy si, gdy oprogramowanie nie jest ju dostpne do uytku. Zazwyczaj cykl ycia oprogramowania zawiera faz koncepcji, faz wymaga, faz projektowania, faz implementacji, faz testw, faz instalacji i zastpowania, faz wykorzystania produkcyjnego i pielgnowania oraz - czasami - faz wycofania. Uwaga: te fazy mog na siebie nachodzi lub mog by wykonywane iteracyjnie."},
    { a: "czarnoskrzynkowa technika projektowania przypadkw testowych", q: "Procedura wyprowadzajca i/lub wybierajca przypadki testowe w oparciu o analiz specyfikacji, funkcjonalnej lub niefunkcjonalnej, moduu lub systemu bez odniesienia do jego wewntrznej struktury."},
    { a: "dane testowe", q: "Dane, ktre istniej (przykadowo w bazie danych) przed wykonaniem testu, i ktre maj wpyw na testowany modu lub system, lub na ktre wywiera wpyw testowany modu lub system."},
    { a: "dane wejciowe do testw", q: "Dane otrzymywane z zewntrznego rda dostarczane do obiektu testw podczas wykonywania testu. rdem zewntrznym moe by sprzt, oprogramowanie lub czowiek."},
    { a: "debagowanie", q: "Proces wyszukiwania, analizowania i usuwania przyczyn awarii oprogramowania."},
    { a: "decyzja", q: "Punkt w programie, w ktrym przepyw sterowania ma dwie lub wicej alternatywnych drg. Wze grafu przepywu sterowania, z ktrego wychodz dwie lub wicej gazi."},
    { a: "defekt", q: "Wada moduu lub systemu, ktra moe spowodowa, e modu lub system nie wykona zakadanej czynnoci, np. niepoprawne wyraenie lub definicja danych. Defekt, ktry wystpi podczas uruchomienia programu, moe spowodowa awari moduu lub systemu."},
    { a: "diagram stanw", q: "Diagram, ktry przedstawia stany, jakie modu lub system moe przyj oraz pokazuje zdarzenia lub okolicznoci, ktre powoduj zmiany stanw i/lub wynikaj z tych zmian. "},
    { a: "dziedzina", q: "Zbir, z ktrego wybierane s wartoci wejcia lub wyjcia."},
    { a: "efekt prbnika", q: "Efekt wpywu elementu pomiarowego na modu lub system podczas dokonywania pomiaru, np. poprzez narzdzie do testw wydajnociowych. Przykadowo wydajno testowanego oprogramowania moe by nieznacznie gorsza, kiedy stosowane jest narzdzie do testw wydajnociowych."},
    { a: "efekt testu", q: "Zbir kryteriw wyjcia."},
    { a: "element testowy", q: "Pojedynczy element, ktry naley przetestowa. Na og jest jeden przedmiot testw i wiele elementw testowych."},
    { a: "emulator", q: "Urzdzenie, program komputerowy lub system, ktry przyjmuje takie same wejcia i generuje takie same wyjcia jak dany system."},
    { a: "faza testw", q: "Wyrniony zbir aktywnoci testowych zebrany w podlegajc zarzdzaniu faz projektu, np. wykonanie testw na jakim poziomie testw."},
    { a: "faza wykonania testu", q: "Okres w cyklu tworzenia oprogramowania, w trakcie ktrego s wykonywane moduy oprogramowania, a samo oprogramowanie jest oceniane w celu zdecydowania, czy wymagania zostay spenione."},
    { a: "faza wymaga", q: "Przedzia czasu w cyklu ycia oprogramowania, podczas ktrego wymagania na oprogramowanie s zbierane i dokumentowane."},
    { a: "funkcjonalno", q: "Zdolno oprogramowania do zapewnienia funkcji odpowiadajcych zdefiniowanym i przewidywanym potrzebom, gdy oprogramowanie jest uywane w okrelonych warunkach."},
    { a: "gsto usterek", q: "Liczba usterek znalezionych w module lub systemie przypadajca na jednostk wielkoci moduu lub systemu (wyraon za pomoc standardowej miary oprogramowania, np. w liniach kodu, liczbie klas lub punktach funkcyjnych)."},
    { a: "gwny plan testw", q: "Plan testw, ktry odnosi si do wielu poziomw testw. "},
    { a: "harmonogram testw", q: "Lista aktywnoci, zada lub zdarze z procesu testowego, okrelajca ich zamierzon dat rozpoczcia i zakoczenia i/lub czas realizacji oraz ich wspzalenoci."},
    { a: "harmonogram wykonania testu", q: "Schemat wykonania procedur testowych. Uwaga! Procedury testowe s zawarte w harmonogramie wykonania testw, w ich kontekcie i w kolejnoci, w jakiej maj by wykonane."},
    { a: "hipercze", q: "Wskanik w dokumencie elektronicznym, ktry stanowi odwoanie do innego dokumentu elektronicznego."},
    { a: "historyjka uytkownika", q: "Wysokopoziomowe wymaganie uytkownika lub wymaganie biznesowe, czsto uywane w zwinnym wytwarzaniu oprogramowania, w typowych sytuacjach skadajce si z jednego lub wicej zda w codziennym lub biznesowym jzyku, opisujce funkcjonalno potrzebn uytkownikowi, kryteria niefunkcjonalne oraz kryteria akceptacji."},
    { a: "identyfikacja ryzyka", q: "Proces identyfikacji ryzyk wykorzystujcy takie techniki jak burza mzgw, listy kontrolne, histori awarii."},
    { a: "implementacja testw", q: "Proces projektowania i nadawania priorytetw procedurom testowym, tworzenie danych testowych i opcjonalnie, przygotowywania jarzma testowego, pisania automatycznych skryptw testowych, infrastruktury testowej: organizacyjnych artefaktw potrzebnych do wykonania testw skadajcych si ze rodowisk testowych, narzdzi testowych, wyposaenia biurowego i procedur postpowania; danych testowych: danych pozyskiwanych z zewntrznych rde przez obiekty testowe podczas wykonywania testw. Zewntrznym rdem moe by sprzt, oprogramowanie lub czowiek."},
    { a: "incydent", q: "Kade zdarzenie wymagajce zbadania."},
    { a: "inspekcja", q: "Rodzaj przegldu koleeskiego polegajcy na wizualnej weryfikacji dokumentw w celu wykrycia defektw, np. niezgodnoci ze standardami projektowymi lub dokumentacj wyszego poziomu. Jest to najbardziej formalna technika przegldu, zawsze oparta na udokumentowanej procedurze."},
    { a: "instrukcja", q: "Element jzyka programowania, ktry jest zwykle najmniejsz niepodzieln jednostk wykonania."},
    { a: "instrukcja wykonywalna", q: "Wyraenie, ktre w trakcie kompilacji jest tumaczone na kod binarny, i ktre bdzie wykonywane proceduralnie podczas dziaania programu. Moe ono wykonywa akcje na danych programu."},
    { a: "integracja", q: "Proces czenia moduw lub systemw w wiksze zespoy."},
    { a: "iteracyjny model wytwarzania", q: "Metoda wytwarzania oprogramowania, w ktrej projekt jest podzielony na du ilo iteracji. Iteracja stanowi zamknity cykl wytwrczy dajcy w wyniku dziaajc wersj produktu (wewntrzn lub zewntrzn) bdc podzbiorem finalnego produktu, ktry rozrasta si z iteracji na iteracj a do powstania produktu kocowego."},
    { a: "jako", q: "Stopie, w jakim modu, system lub proces spenia okrelone wymagania i/lub spenia potrzeby i oczekiwania klienta lub uytkownika."},
    { a: "jako danych", q: "Cecha danych, ktra pokazuje ich poprawno wzgldem predefiniowanych kryteriw, np. wymaga biznesowych, wymaga na integralno danych, spjno danych."},
    { a: "jako oprogramowania", q: "Og funkcjonalnoci i cech oprogramowania, ktre charakteryzuj zdolno zaspokajania stwierdzonych lub przewidywanych potrzeb."},
    { a: "jarzmo testowe", q: "rodowisko testowe, skadajce si z zalepek i sterownikw potrzebnych do wykonania testu."},
    { a: "jzyk skryptowy", q: "Jzyk programowania, w ktrym pisane s skrypty testowe wykorzystywane przez narzdzie do uruchamiania testw (np. narzdzie rejestrujco-odtwarzajce)."},
    { a: "kamie milowy", q: "Punkt w czasie realizacji projektu, dla ktrego zostay okrelone (porednie) produkty oraz wyniki."},
    { a: "kierownik testw", q: "Osoba odpowiedzialna za zarzdzanie projektem w zakresie czynnoci zwizanych z testowaniem, w zakresie zasobw uczestniczcych w testowaniu oraz oceny przedmiotu testu. Osoba, ktra kieruje, zarzdza, administruje, planuje i reguluje ocen przedmiotu testu."},
    { a: "klasa rwnowanoci", q: "Podzbir dziedziny danych wejciowych lub wyjciowych, dla ktrego zakada si, na podstawie specyfikacji, e zachowanie moduu lub systemu jest takie samo."},
    { a: "kod", q: "Instrukcje komputera i definicje danych wyraone w jzyku programowania lub w postaci wyjciowej w asemblerze, kompilatorze lub translatorze."},
    { a: "komparator testowy", q: "Narzdzie testowe do przeprowadzania automatycznego porwnania rezultatw rzeczywistych z oczekiwanymi."},
    { a: "kompilator", q: "Narzdzie, ktre tumaczy programy napisane w jzyku wysokiego poziomu na ich odpowiedniki w jzyku maszynowym."},
    { a: "konfiguracja", q: "Ukad moduw lub system zdefiniowany za pomoc liczb, poprzez swoj natur, oraz przez poczenia pomidzy czciami skadowymi."},
    { a: "kontrola konfiguracji", q: "Element zarzdzania konfiguracj skadajcy si z oceny, koordynacji oraz udzielenia lub nieudzielenia zgody na zmian elementw konfiguracji oraz implantacji zmian po formalnej identyfikacji elementu konfiguracji."},
    { a: "kreator instalacji", q: "Oprogramowanie dostarczone na odpowiednich nonikach, ktre prowadzi instalatora przez proces instalacji. Zazwyczaj wykonuje proces instalacji, informuje o jego wynikach i prosi o wybr opcji."},
    { a: "kryteria akceptacji", q: "Kryteria wyjcia, ktre modu lub system musi spenia, aby zosta zaakceptowany przez uytkownika, klienta lub inny uprawniony podmiot."},
    { a: "kryteria wznowienia", q: "Kryteria uywane do wznowienia czci lub caoci aktywnoci testowych, ktre to czynnoci zostay uprzednio wstrzymane."},
    { a: "kryteria zaliczenia/nie zaliczenia", q: "Reguy decyzyjne wykorzystywane do okrelenia czy obiekt testowany (funkcja) lub cecha zaliczya test."},
    { a: "kryteria zawieszenia", q: "Kryteria uywane do (tymczasowego) zatrzymania wszystkich lub czci aktywnoci testowych na elementach testowych."},
    { a: "kryterium wejcia", q: "Zbir oglnych i specyficznych warunkw, ktrych spenienie jest wymagane do kontynuacji procesu dla okrelonego zadania, np. fazy testw. Celem kryterium wejcia jest ochrona przed rozpoczciem zadania, w sytuacji, gdy pociga to za sob wicej (zmarnowanych) nakadw pracy w porwnaniu z nakadem pracy potrzebnym do osignicia stanu spenienia kryterium wejcia. "},
    { a: "kryterium wyjcia", q: "Zbir oglnych i specyficznych warunkw, uzgodnionych z udziaowcami, ktrych spenienie jest wymagane do oficjalnego zakoczenia procesu. Celem kryterium wyjcia jest ochrona przed uznaniem zadania za ukoczone w przypadku, gdy jakie jego elementy nie s jeszcze w peni wykonane. Kryteria wyjcia s stosowane jako argument przeciwko zakoczeniu testw oraz do planowania, kiedy mona to zrobi."},
    { a: "log (dziennik) testw", q: "Chronologiczny zapis szczegw zwizanych z wykonaniem testw."},
    { a: "logowanie testu", q: "Proces rejestrowania informacji o wykonanych testach w logu testw."},
    { a: "mapie testowanie", q: "Metoda testowania polegajc na losowym wyborze z szerokiego zakresu wej i losowym naciskaniu przyciskw, ignorujc sposb, w jaki produkt powinien by uywany."},
    { a: "manifest Agile", q: "Okrelenie zasad, ktre stanowi zwinne wytwarzanie oprogramowania. Te zasady to: (1) ludzie i wsppraca ponad procesy i narzdzia; (2) dziaajce oprogramowanie ponad obszern dokumentacj; (3) wsppraca z klientem ponad formalne ustalenia; (4) reagowanie na zmiany ponad podanie za planem."},
    { a: "mapa myli", q: "Diagram uywany do przedstawiania sw, idei, zada, lub innych elementw zwizanych i ukadajcych si wok centralnego sowa lub idei. Mapa myli jest uywana do generowania, wizualizacji, strukturalizowania i klasyfikowania idei oraz jako rodek pomocniczy w poznawaniu, organizacji, rozwizywaniu problemw, podejmowaniu decyzji i pisaniu."},
    { a: "maskowanie defektw", q: "Sytuacja, w ktrej wystpowanie jednego defektu uniemoliwia wykrycie innego."},
    { a: "matryca odpowiedzialnoci  model RACI", q: "Macierz przedstawiajca udzia rnych rl w wykonywaniu zada lub produktw w projekcie lub procesie. Jest ona szczeglnie uyteczna w wyjanianiu rl i odpowiedzialnoci. RACI jest akronimem wyprowadzonym z czterech kluczowych najczciej uywanych odpowiedzialnoci: Odpowiedzialny (ang. Responsible), Rozliczany (ang. Accountable), Konsultujcy (ang. Consulted) i Informowany (ang. Informed)."},
    { a: "metryka", q: "Skala pomiaru i sposb jej stosowania."},
    { a: "misja testowania", q: "Cel testowania w organizacji, czsto dokumentowany jako cz polityki testw."},
    { a: "model cyklu ycia", q: "Podzia ycia produktu lub projektu na fazy."},
    { a: "model V", q: "Opis czynnoci cyklu ycia wytwarzania oprogramowania od specyfikacji wymaga do pielgnacji. Model V ilustruje jak czynnoci testowe mog by integrowane z kadym etapem cyklu ycia wytwarzania oprogramowania."},
    { a: "moderator", q: "Lider i gwna osoba odpowiedzialna za prowadzenie inspekcji lub przegldu."},
    { a: "modu", q: "Najmniejszy element programu, ktry bdzie testowany w izolacji - niezalenie od innych moduw."},
    { a: "monitorowanie testw", q: "Zadanie w zarzdzaniu testowaniem, zajmujce si okresowym sprawdzaniem statusu projektu testowego. Przygotowywane s raporty porwnujce stan aktualny z planowanym. Patrz take zarzdzanie testami."},
    { a: "nadzr nad testami", q: "Zadanie z zakresu zarzdzania\ntestem, ktrego celem jest opracowanie i zastosowanie dziaa korygujcych projekt testowy, kiedy monitowanie pokazuje odchylenie od planu. "},
    { a: "najlepsza praktyka", q: "Zalecana metoda albo nowatorska praktyka, ktra przyczynia si do lepszych wynikw organizacji w konkretnym kontekcie, zwykle uznawana za \"najlepsz\" przez inne podobne organizacje."},
    { a: "narzdzie do analizy dynamicznej", q: "Narzdzie rejestrujce informacje o stanie wykonywanego programu. Takie narzdzia uywane s najczciej do znajdowania nieprzypisanych wskanikw, sprawdzanie alokacji, uycia i dealokacji pamici oraz do oznaczania jej wyciekw."},
    { a: "narzdzie do debagowania", q: "Narzdzie uywane przez programistw do odtwarzania awarii, ledzenia stanu programu i odszukiwania usterek. Debagery umoliwiaj programistom wykonywanie programw krok po kroku, zatrzymywanie wykonania programu na dowolnej instrukcji oraz sprawdzanie i ustawianie wartoci zmiennych."},
    { a: "narzdzie do modelowania", q: "Narzdzie, ktre wspiera walidacj modeli oprogramowania lub systemw."},
    { a: "narzdzie do projektowania testu", q: "Narzdzie, ktre wspiera projektowanie testu generujc wejcia ze specyfikacji (przechowywanej na przykad w repozytorium systemu typu CASE jak narzdzie zarzdzania wymaganiami), z wyspecyfikowanych warunkw testu przechowywanych w samym narzdziu lub z kodu."},
    { a: "narzdzie do przygotowywania danych testowych", q: "Rodzaj narzdzia testowego, ktre pozwala na wybranie danych z istniejcej bazy danych lub ich stworzenie, wygenerowanie, przetworzenie i edycj dla uycia w testowaniu."},
    { a: "narzdzie do testowania obcienia", q: "Narzdzie wspierajce testy obcieniowe, za pomoc ktrego mona symulowa narastajce obcienie, np. ilo aktualnie pracujcych uytkownikw i/lub liczb transakcji w okrelonym czasie."},
    { a: "narzdzie do testowania przeciajcego", q: "Narzdzie, ktre wspiera testowanie przeciajce."},
    { a: "narzdzie do testw wydajnociowych", q: "Narzdzie wspierajce testowanie wydajnociowe, zazwyczaj majce dwie funkcjonalnoci: generacja obcienia i pomiar transakcji. Generowane obcienie moe symulowa zarwno wielu uytkownikw, jak i du ilo wprowadzanych danych. W czasie wykonywania testw logowane s tylko pomiary z wybranych transakcji. Narzdzia do testw wydajnociowych zazwyczaj dostarczaj raporty bazujce na logowanych transakcjach oraz wykresy obcienia w zalenoci od czasw odpowiedzi."},
    { a: "narzdzie do testw zabezpiecze", q: "Narzdzie wspierajce testowanie charakterystyk zabezpiecze i podatnoci."},
    { a: "narzdzie do wykonywania testu", q: "Rodzaj narzdzia testowego, ktre jest w stanie wykona inne oprogramowanie uywajc testowych skryptw automatycznych, np. narzdzie rejestrujco-odtwarzajce."},
    { a: "narzdzie do zabezpiecze", q: "Narzdzie, ktre wspiera zabezpieczenia operacyjne."},
    { a: "narzdzie do zarzdzania defektami", q: "Narzdzie wspomagajce zapisywanie informacji o defektach i ledzenie ich statusu oraz zmian. Takie narzdzia czsto posiadaj funkcje ledzenia i nadzorowania przepywu pracy zwizanego z przypisywaniem, poprawianiem i re-testowaniem defektw oraz posiadaj funkcje raportowe."},
    { a: "narzdzie do zarzdzania incydentami", q: "Narzdzia\nuatwiajce rejestracj incydentw i ledzenie ich statusw. Czsto oferuj funkcje ledzenia i kontroli przepywu pracy zwizanego z przydziaem, napraw i retestami. Zapewniaj rwnie moliwo raportowania."},
    { a: "narzdzie do zarzdzania konfiguracj", q: "Narzdzie wspomagajce identyfikacj oraz kontrol elementw konfiguracji, ich statusw i wersji podczas wykonywania zmian oraz tworzenia wyda/publikacji, w ktrych skad wchodz."},
    { a: "narzdzie do zarzdzania testami", q: "Narzdzie wspomagajce zarzdzanie testami i sterowanie czci procesu testowego. Zwykle ma kilka funkcji takich jak zarzdzanie testaliami, planowanie testw w czasie, logowanie rezultatw, ledzenie postpu prac, zarzdzanie incydentami oraz raportowanie wynikw testw."},
    { a: "narzdzie do zarzdzania wymaganiami", q: "Narzdzie wspierajce rejestrowanie wymaga i ich atrybutw posiadajce zdolno ledzenia powiza miedzy warstwami wymaga i zarzdzania zmianami w wymaganiach. Niektre narzdzia posiadaj funkcjonalnoci umoliwiajce analiz statyczn np. sprawdzanie zwizoci wymaga lub odstpstw od zdefiniowanych regu dotyczcych wymaga."},
    { a: "narzdzie mierzce pokrycie", q: "Narzdzie dajce obiektywn miar, ktre elementy strukturalne (np. instrukcje, gazie) zostay sprawdzone przez zestaw testowy."},
    { a: "narzdzie o otwartym kodzie", q: "Narzdzie programistyczne, ktre jest dostpne dla wszystkich potencjalnych uytkownikw w postaci kodu rdowego, zwykle poprzez internet. Na og w postaci licencji na badanie, zmienianie, poprawianie i czasami dystrybuowanie oprogramowania."},
    { a: "narzdzie rejestrujco-odtwarzajce", q: "Rodzaj narzdzia do wykonywania testu, w ktrym dane wejciowe s nagrane podczas testw manualnych w celu wygenerowania automatycznych skryptw, ktre mog by uruchomione pniej (tzn. powtrzone). Narzdzia te s czsto uywane do wspierania automatycznego testowania regresyjnego."},
    { a: "narzdzie testowe", q: "Oprogramowanie, ktre wspomaga jedn lub kilka czynnoci testowych, takich jak planowanie, zarzdzanie, specyfikacja, budowa pocztkowych plikw i danych, wykonanie i analiza testw."},
    { a: "narzdzie wspomagajce przegld", q: "Narzdzie, ktre zapewnia wsparcie w procesie przegldu. Typowe cechy narzdzia to planowanie przegldu i wspomaganie ledzenia, wspomaganie komunikacji oraz wsppracy przy przegldach, a take prowadzenie repozytorium do gromadzenia i raportowania metryk."},
    { a: "nieosigalny kod", q: "Kod, ktry nie moe by osignity i dlatego te nie jest wykonywany."},
    { a: "niezaleno testowania", q: "Rozdzielenie odpowiedzialnoci, ktre sprzyja zapewnieniu obiektywnoci testowania."},
    { a: "niezawodno", q: "Zdolno oprogramowania do wykonywania wymaganych funkcji w okrelonych warunkach przez okrelony czas lub dla okrelonej liczby operacji."},
    { a: "niezdany", q: "Test uznaje si za niezdany, jeli jego rzeczywisty rezultat nie zgadza si z oczekiwanym."},
    { a: "niezgodno", q: "Niespenienie konkretnego wymagania."},
    { a: "norma", q: "Formalny, o ile to moliwe obowizkowy, zbir wymaga stworzony i uywany do opisu zalecanego spjnego podejcia do sposobu pracy lub do dostarczania wytycznych (np. normy ISO/IEC, normy IEEE lub normy w organizacjach)."},
    { a: "obsuga wyjtkw", q: "Zachowanie moduu lub systemu w odpowiedzi na bdne wejcie wprowadzone przez uytkownika lub inny modu czy system."},
    { a: "oczekiwany rezultat", q: "Zachowanie moduu lub systemu w okrelonych warunkach okrelone na podstawie specyfikacji lub innego rda."},
    { a: "odtwarzalno", q: "Zdolno oprogramowania do osigania okrelonego poziomu wydajnoci i przywracania danych uszkodzonych przez awari."},
    { a: "oprogramowanie", q: "Programy komputerowe, procedury, dokumentacja i dane majce zwizek z dziaaniem systemu komputerowego."},
    { a: "oprogramowanie z pki", q: "Oprogramowanie stanowice produkt wytworzony na szeroki rynek, tj. dla duej liczby klientw, ktre jest dostarczane wielu klientom w identycznej postaci."},
    { a: "pielgnowalno", q: "atwo, z ktr oprogramowanie moe by modyfikowane w celu naprawy defektw, dostosowania do nowych wymaga, modyfikowane w celu uatwienia przyszego utrzymania lub dostosowania do zmian zachodzcych w jego rodowisku. "},
    { a: "plan przegldu", q: "Dokument opisujcy podejcie, zasoby i harmonogram zamierzonych czynnoci zwizanych z przegldem. Identyfikuje on, m.in. dokumenty i kod podlegajcy przegldowi, typy przegldw do wykorzystania, uczestnikw, a take kryteria wejcia i wyjcia, ktre bd stosowane w przegldach formalnych, a take uzasadnienie ich wyboru. Jest zapisem procesu planowania przegldu."},
    { a: "plan testw", q: "Dokument opisujcy zakres, metody, zasoby oraz harmonogram zamierzonych czynnoci testowych. Okrela - midzy innymi - elementy testowe, testowane cechy, zadania testowe, kto bdzie te zadania wykonywa, stopie niezalenoci testerw, rodowisko testowe, technik projektowania testw oraz kryteria wejcia i wyjcia, przesanki ich uycia, a take ryzyka wymagajce cigego planowania. Jest to zapis procesu planowania testw."},
    { a: "planowanie testw", q: "Czynno tworzenia planw testw lub wprowadzanie do nich zmian."},
    { a: "podejcie do testu", q: "Implementacja strategii testw dla konkretnego projektu. Zwykle zawiera decyzje podjte na podstawie celw i analizy ryzyka projektu (testowego), punkty startowe procesu testowego, techniki projektowania testu do wykorzystania, kryteria wyjcia i typy testu do wykonania."},
    { a: "podstawa", q: "Specyfikacja lub oprogramowanie, ktre byo poddane formalnemu przegldowi lub uprzednio uzgodnione, bdce odniesieniem dla dalszych prac programistycznych, ktre moe ulec zmianie tylko przez formaln procedur zmian."},
    { a: "podstawa testu", q: "Wszystkie dokumenty, z ktrych mona wywnioskowa wymagania dla moduu lub systemu. Dokumentacja, na podstawie ktrej oparte s przypadki testowe. Jeli dokument moe by zmieniony tylko poprzez formaln procedur zmiany, to podstawa testu nazywana jest zamroon podstaw testu."},
    { a: "podstawowe testowanie porwnawcze", q: "Czarnoskrzynkowa technika projektowania przypadkw testowych, w ktrej przypadki testowe projektowane s tak, by wykona kombinacj wej wykorzystujc metod testowania zmodyfikowanego pokrycia warunkw wielokrotnych."},
    { a: "podstawowy zestaw testw", q: "Zestaw przypadkw testowych powstay na podstawie wewntrznej struktury moduu lub specyfikacji, ktry zapewnia osignicie 100% okrelonego kryterium pokrycia."},
    { a: "podzia na klasy rwnowanoci", q: "Czarnoskrzynkowa technika projektowania przypadkw testowych, w ktrej przypadki testowe projektowane s tak, aby uy elementw z klas rwnowanoci. W szczeglnoci przypadki testowe projektowane s tak, aby co najmniej raz pokry kad klas rwnowanoci (tzn. by z kadej klasy rwnowanoci wzi jednego reprezentanta)."},
    { a: "pokrycie decyzji", q: "Odsetek moliwych wynikw decyzji, ktre zostay przetestowane przez zestaw testowy. 100% pokrycia decyzji jest rwnowany 100% pokrycia gazi oraz implikuje 100% pokrycia instrukcji."},
    { a: "pokrycie gazi", q: "Odsetek gazi sprawdzonych przez zestaw przypadkw testowych. 100% pokrycia gazi implikuje 100% pokrycia decyzji i 100% pokrycia instrukcji."},
    { a: "pokrycie instrukcji kodu", q: "Odsetek instrukcji wykonywalnych, ktre zostay przetestowane przez zestaw testowy."},
    { a: "pokrycie klas rwnowanoci", q: "Odsetek klas rwnowanoci, ktre zostay sprawdzone przez zestaw testw."},
    { a: "pokrycie kodu", q: "Metoda analityczna, okrelajca ktre czci programu zostay wykonane (pokryte) przez zestaw testowy, oraz ktre czci nie zostay wykonane, np. pokrycie instrukcji kodu, pokrycie decyzji, pokrycie warunkw."},
    { a: "pokrycie wartoci brzegowych", q: "Odsetek wartoci brzegowych, ktry zosta sprawdzony przez zestaw przypadkw testowych."},
    { a: "pokrycie warunkw", q: "Odsetek pokrycia warunkw, jaki zosta sprawdzony przez zestaw testw. 100% pokrycia warunkw wymaga, aby kady pojedynczy warunek kadego wyraenia decyzyjnego zosta przetestowany zarwno dla wartoci Prawda jak i Fasz."},
    { a: "pokrycie warunkw decyzji", q: "Odsetek wszystkich moliwych wynikw warunkw prostych w decyzjach, ktre zostay sprawdzone przez zestaw testowy; 100% pokrycia warunkw decyzji implikuje 100% pokrycia warunkw oraz 100% pokrycia decyzji."},
    { a: "polityka testw", q: "Dokument wysokiego poziomu opisujcy zasady, podejcie i gwne zadania organizacji dotyczce testowania."},
    { a: "pomyka", q: "Patrz bd."},
    { a: "powtarzalno testw", q: "Atrybut testu wskazujcy czy przy kadym wykonaniu testu otrzymujemy te same wyniki."},
    { a: "poziom integralnoci oprogramowania", q: "Stopie, w jakim oprogramowanie spenia lub musi spenia zbir wybranych przez interesariuszy cech oprogramowania lub charakterystyk systemu (np. zoono oprogramowania, ocenione ryzyko, poziom zabezpiecze, poziom bezpieczestwa, podana wydajno, niezawodno lub koszt), ktre to cechy obrazuj, jak wane jest dane oprogramowanie dla jego interesariuszy."},
    { a: "poziom ryzyka", q: "Okrelenie istotnoci ryzyka zdefiniowane przez jego waciwoci: wpyw i prawdopodobiestwo. Poziom ryzyka moe by uyty do okrelenia poziomu testw, ktre naley przeprowadzi."},
    { a: "poziom testw", q: "Grupa czynnoci testowych, ktre s razem zorganizowane i zarzdzane. Poziom testw jest powizany z poziomami odpowiedzialnoci w projekcie. Przykadami poziomw testw s testy moduowe, integracyjne, systemowe i akceptacyjne."},
    { a: "priorytet", q: "Poziom (biznesowej) wanoci okrelony dla elementu np. defektu."},
    { a: "produkt", q: "Dowolny produkt pracy, ktry musi by dostarczony komu innemu ni jego autor."},
    { a: "profil obcienia", q: "Specyfikacja aktywnoci uytkownikw, jakiej testowany modu lub system moe dowiadczy w trakcie uycia operacyjnego. Profil obcienia skada si z okrelonej liczby wirtualnych uytkownikw, ktrzy przetwarzaj zdefiniowany zbir transakcji w okrelonym przedziale czasowym i zgodnie z predefiniowanym profilem produkcyjnym."},
    { a: "programowanie ekstremalne (extreme programming XP)", q: "Metodyka inynierii oprogramowania uywana w ramach zwinnego wytwarzania oprogramowania. Jej podstawowe praktyki to programowanie w parach, wykonywanie dokadnych przegldw kodu, testowanie moduowe caego kodu, jasno i przejrzysto kodu."},
    { a: "programowanie parami", q: "Metoda wytwarzania oprogramowania, w ktrej linie kodu (produkcyjne i/lub testowe) moduu s pisane przez dwch programistw siedzcych przy jednym komputerze. Domylnie oznacza to odbywajcy si w czasie rzeczywistym przegld kodu."},
    { a: "projekt", q: "Projekt jest to zestaw skoordynowanych i kontrolowanych aktywnoci o okrelonym czasie rozpoczcia i zakoczenia. Powoywany jest w celu osignicia celu zgodnie z okrelonymi wymaganiami wczajc w nie ograniczenia czasowe, kosztowe i zasobowe."},
    { a: "projektowanie funkcjonalnych przypadkw testowych", q: "Procedura projektowania i/lub wyboru przypadkw testowych w oparciu o analiz specyfikacji funkcjonalnej moduu lub systemu bez zagldania w jego wewntrzn struktur. Patrz take czarnoskrzynkowa technika projektowania przypadkw testowych."},
    { a: "projektowanie przypadkw testowych w oparciu o specyfikacj", q: "Patrz czarnoskrzynkowa technika projektowania przypadkw testowych."},
    { a: "projektowanie strukturalnych przypadkw testowych", q: "Patrz biaoskrzynkowe techniki projektowania."},
    { a: "protoklant (skryba)", q: "Osoba, ktra podczas spotkania przegldowego rejestruje w dzienniku przegldu kady zgoszony defekt lub sugesti odnonie usprawnienia procesu. Protoklant musi zapewni, e dziennik przegldu jest czytelny i zrozumiay."},
    { a: "przebieg testu", q: "Wykonanie testu na okrelonej wersji przedmiotu testu."},
    { a: "przedmiot pokrycia", q: "Obiekt lub waciwo wykorzystywane jako punkt wyjcia dla zaprojektowania testw pokrycia, np. klasy rwnowanoci lub pokrycie kodu."},
    { a: "przedmiot testw", q: "Modu lub system podlegajcy testowaniu."},
    { a: "przegld", q: "Ocena produktu lub statusu projektu majca na celu stwierdzenie rozbienoci od planowanych zaoe i rekomendacj usprawnie. Przykady: przegld kierowniczy, przegld nieformalny, przegld techniczny, inspekcja, przejrzenie."},
    { a: "przegld formalny", q: "Przegld charakteryzujcy si udokumentowanymi procedurami i wymaganiami, np. inspekcja."},
    { a: "przegld koleeski", q: "Przegld produktw powstaych podczas wytwarzania oprogramowania przeprowadzany przez kolegw ich twrcy majcy na celu wskazanie defektw i moliwoci poprawek. Przykadami przegldw s inspekcje, przegld techniczny oraz przejrzenie."},
    { a: "przegld nieformalny", q: "Przegld, ktry nie jest oparty na formalnej (udokumentowanej) procedurze."},
    { a: "przegld techniczny", q: "Dyskusja w grupie wsppracownikw skupiajca si na osigniciu porozumienia w zakresie aspektw technicznych."},
    { a: "przegldajcy", q: "Osoba zaangaowana w przegld, ktra identyfikuje i opisuje odstpstwa w przegldanym produkcie lub projekcie. Przegldajcy mog by dobierani tak, aby reprezentowa rne punkty widzenia i peni rne role w procesie przegldu."},
    { a: "przejrzenie", q: "Przedstawienie przez autora, krok po kroku, dokumentu w celu zebrania informacji i ustalenia wsplnego rozumienia jego zawartoci."},
    { a: "przenaszalno", q: "atwo z jak oprogramowanie moe by przeniesione z jednego rodowiska sprztowego lub programowego do innego rodowiska."},
    { a: "przepyw danych", q: "Abstrakcyjna reprezentacja sekwencji i moliwych zmian stanu obiektu danych, gdzie dostpne stany obiektu to utworzenie, uycie lub usunicie."},
    { a: "przepyw sterowania", q: "Sekwencja zdarze (w postaci cieki) majca miejsce podczas pracy moduu lub systemu."},
    { a: "przetestowany", q: "Element oprogramowania okrela si jako przetestowany (sprawdzony) przez przypadek testowy, kiedy warto wejciowa powoduje wykonanie tego elementu, np. instrukcji, decyzji lub innego elementu strukturalnego."},
    { a: "przewodnik instalacji", q: "Instrukcje dostarczone na odpowiednich nonikach, ktre prowadz instalatora przez proces instalacji. Moe to by podrcznik, procedura krok po kroku, kreator lub dowolny inny opis procesu."},
    { a: "przypadek testowy", q: "Zbir danych wejciowych, wstpnych warunkw wykonania, oczekiwanych rezultatw i kocowych warunkw wykonania opracowany w okrelonym celu lub dla warunku testowego, jak wykonanie pewnej cieki programu lub zweryfikowanie zgodnoci z konkretnym wymaganiem."},
    { a: "przypadek testowy niskiego poziomu", q: "Przypadek testowy z konkretnymi (na poziomie implementacji) wartociami wejciowymi i wynikami oczekiwanymi. Logiczne operatory z przypadkw testowych wysokiego poziomu s zamieniane na konkretne wartoci, ktre odpowiadaj celom logicznych operatorw."},
    { a: "przypadek testowy wysokiego poziomu", q: "Przypadek testowy bez konkretnych (poziom implementacji) wartoci danych wejciowych i oczekiwanych rezultatw. Uywane s operatory logiczne; rzeczywiste wartoci nie s jeszcze zdefiniowane i/ lub dostpne."},
    { a: "przypadek uycia", q: "Cig transakcji w dialogu pomidzy uytkownikiem a systemem z namacalnym rezultatem."},
    { a: "przyrostowy model wytwarzania", q: "Model wytwarzania oprogramowania, w ktrym przedsiwzicie realizowane jest przyrostowo, w cyklach, z ktrych kady dostarcza cz funkcjonalnoci z caego zbioru wymaga. Wymagania s porzdkowane wedug priorytetw i realizowane w tej kolejnoci w odpowiednich przyrostach. W niektrych, (ale nie wszystkich) wersjach tego modelu wytwarzania, kady podprojekt jest realizowany zgodnie z mini modelem V z fazami: projektowania, kodowania i testowania."},
    { a: "pseudolosowy", q: "Cig, ktry wyglda na losowy, ale w rzeczywistoci zosta wygenerowany wedug pewnej uprzednio przygotowanej sekwencji."},
    { a: "punkt startu", q: "Pierwsze wyraenie wykonywalne wewntrz moduu."},
    { a: "punkt wyjcia", q: "Ostatnie wyraenie wykonywalne wewntrz moduu."},
    { a: "raport o incydencie", q: "Dokument opisujcy jakiekolwiek zdarzenie, ktre nastpio np. podczas testowania wymagajce dalszego zbadania."},
    { a: "raport o postpie testw", q: "Dokument zawierajcy podsumowanie aktywnoci testowych i osignitych wynikw, tworzony regularnie, by zaraportowa postp prac testowych w stosunku do zaoe (takich jak np. oryginalny plan testw) i przedstawiajcy ryzyka i alternatywy wymagajce podjcia decyzji zarzdczych."},
    { a: "raport z testw", q: "Patrz sumaryczny raport z testw"},
    { a: "rejestracja incydentu", q: "Dokumentowanie szczegw zdarzenia, ktre wystpio, na przykad podczas testw."},
    { a: "retestowanie", q: "Testowanie polegajce na uruchomieniu przypadkw testowych, ktre podczas ostatniego uruchomienia wykryy bdy, w celu sprawdzenia poprawnoci naprawy."},
    { a: "retrospektywa projektu", q: "Ustrukturalizowane podejcie do zapoznania si z wycignitymi wnioskami i stworzenia konkretnych planw dziaa majcych na celu popraw nastpnego projektu lub nastpnego etapu projektu."},
    { a: "rezultat", q: "Konsekwencja wyniku wykonania testu. Zawiera on wyjcia na ekran, zmiany danych, raporty oraz wysyane komunikaty."},
    { a: "ryzyko", q: "Czynnik, ktry w przyszoci moe skutkowa negatywnymi konsekwencjami; zazwyczaj opisywany jako wpyw oraz prawdopodobiestwo."},
    { a: "ryzyko produktowe", q: "Ryzyko bezporednio powizane z przedmiotem testw."},
    { a: "ryzyko projektowe", q: "Ryzyko zwizane z zarzdzaniem i kontrol projektu (testowego), np. braki zasobowe, rygorystyczny harmonogram, zmieniajce si wymagania itp. "},
    { a: "rzeczywisty rezultat", q: "Wytworzone/zaobserwowane zachowanie si moduu lub systemu podczas testowania tego moduu lub systemu."},
    { a: "scenariusz testowy", q: "Patrz specyfikacja procedury testowej."},
    { a: "SCRUM", q: "Iteracyjna, przyrostowa struktura zarzdzania projektem, powszechnie stosowana w zwinnym wytwarzaniu oprogramowania."},
    { a: "sesja testowa", q: "W testowaniu eksploracyjnym, nieprzerywalny okres czasu powicony testowaniu. Kada sesja zorientowana jest na kart opisu, ale tester moe w tym czasie take odkrywa nowe moliwoci lub kwestie. Tester tworzy i wykonuje przypadki testowe w locie oraz zapisuje ich postp."},
    { a: "skrypt testowy", q: "Powszechnie uywana nazwa specyfikacji procedury testowej, zwaszcza automatycznej."},
    { a: "specyfikacja", q: "Dokument, ktry okrela, najlepiej w kompletny, precyzyjny i moliwy do weryfikacji sposb, wymagania, projekt, zachowanie lub inne waciwoci moduu lub systemu, oraz czsto procedury sprawdzania, czy te warunki zostay spenione."},
    { a: "specyfikacja procedury testowej", q: "Dokument okrelajcy cig akcji umoliwiajcy wykonanie testu. Znana take jako skrypt testowy lub manualny skrypt testowy."},
    { a: "specyfikacja projektu testw", q: "Dokument specyfikujcy warunki testowe (elementy pokrycia) dla elementu testowego, szczegowe podejcie do testw oraz identyfikujcy powizane przypadki testowe wysokiego poziomu."},
    { a: "specyfikacja przypadkw testowych", q: "Dokument specyfikujcy zbir przypadkw testowych (cel, wejcia, czynnoci testowe, oczekiwane rezultaty i wstpne warunki wykonania) dla elementu testowego."},
    { a: "specyfikacja testw", q: "Dokument zawierajcy specyfikacj projektu testw, specyfikacje przypadkw testowych i/lub specyfikacj procedury testowej."},
    { a: "spotkanie retrospektywne", q: "Spotkanie na kocu projektu, podczas ktrego czonkowie zespou projektowego oceniaj projekt i wycigaj wnioski, ktre mog by wykorzystane w nastpnym projekcie."},
    { a: "sterownik", q: "Modu oprogramowania lub narzdzie testowe, ktre zastpuje modu kontrolujcy lub wywoujcy funkcje testowanego moduu lub systemu."},
    { a: "strategia testw", q: "Wysokopoziomowy opis poziomw testw, ktre maj by wykonane oraz testw w ramach tych poziomw dla organizacji lub programu (jednego lub kilku projektw)."},
    { a: "struktura do testw jednostkowych", q: "Narzdzie, ktre dostarcza rodowisko do testw jednostkowych lub moduowych. W takim rodowisku modu moe by testowany niezalenie (w izolacji) lub z uyciem odpowiednich zalepek i sterownikw. Dostarcza rwnie innego rodzaju wsparcia dla programistw np. moliwo debagowania."},
    { a: "sumaryczny raport z testw", q: "Sumaryczny dokument przedstawiajcy dziaania testowe i ich rezultaty. Zawiera take ocen testowanych elementw pod wzgldem zgodnoci z kryteriami wyjcia."},
    { a: "symulator", q: "Urzdzenie, program komputerowy albo system uywany podczas testowania, ktry przy zadanym zbiorze wej zachowuje si lub dziaa tak jak dany system."},
    { a: "system krytyczny ze wzgldw bezpieczestwa", q: "System, ktrego awaria lub nieprawidowe dziaanie moe skutkowa mierci lub powanymi obraeniami ludzi, utrat lub powanymi uszkodzeniami urzdze, lub zanieczyszczeniem rodowiska."},
    { a: "cieka", q: "Sekwencja wydarze, np. wykonywalnych wyrae, w ramach moduu lub systemu poczwszy od punktu wejcia do punktu wyjcia."},
    { a: "redni czas do naprawy", q: "rednia arytmetyczna czasu, w jakim system bdzie uruchomiony po awarii. Na og jest w tym zawarte testowanie zapewniajce, e defekt zosta usunity."},
    { a: "redni czas pomidzy awariami (MTBF)", q: "rednia arytmetyczna czasw pomidzy awariami systemu. Na og jest to cz modelu wzrostu niezawodnoci, w ktrym zakada si, e awarie systemu s natychmiast naprawiane, jako cz procesu usuwania defektw."},
    { a: "rodowisko produkcyjne", q: "Sprzt i oprogramowanie zainstalowane w siedzibie uytkownika lub klienta, w ktrym modu lub system bdzie uywany. W skad oprogramowania mog wchodzi systemy operacyjne, bazy danych i inne aplikacje."},
    { a: "rodowisko testowe", q: "rodowisko, w skad ktrego wchodzi sprzt, wyposaenie, symulatory, oprogramowanie oraz inne elementy wspierajce, potrzebne do wykonania testu."},
    { a: "tablica decyzyjna", q: "Tablica pokazujca kombinacj wej i/lub czynnikw (przyczyn) z odpowiadajcymi im wyjciami i akcjami (skutkami), pomocna w projektowaniu przypadkw testowych."},
    { a: "tablica stanw", q: "Tablica, ktra dla kadego stanu zestawia przejcia z tego stanu z kadym moliwym zdarzeniem. Obrazuje zarwno dozwolone, jak i niedozwolone przejcia."},
    { a: "technika projektowania testw", q: "Procedura uywana do wywodzenia i/lub wybierania przypadkw testowych."},
    { a: "technika projektowania testw niefunkcjonalnych", q: "Procedura otrzymywania i/lub wyboru przypadkw testowych dla testw niefunkcjonalnych oparta na analizie specyfikacji moduu lub systemu bez odniesienia do jego wewntrznej struktury."},
    { a: "technika projektowania testw oparta na defektach", q: "Procedura projektowania i/ lub wyboru przypadkw testowych ukierunkowana na jeden lub wicej typw defektw, w ktrej testy projektuje si na podstawie wiedzy o okrelonych typach defektw."},
    { a: "technika projektowania testw oparta na dowiadczeniu", q: "Procedura projektowania i/lub wyboru przypadkw testowych w oparciu o dowiadczenie, wiedz i intuicj testera."},
    { a: "technika projektowania testw w oparciu o struktur", q: "patrz biaoskrzynkowe techniki projektowania testw."},
    { a: "technika wykonywania testu", q: "Metoda uyta do wykonania konkretnego testu, zarwno rcznie, jak i automatycznie."},
    { a: "Test", q: "Zestaw jednego lub wicej przypadkw testowych. "},
    { a: "test dymny", q: "Podzbir wszystkich zdefiniowanych / zaplanowanych przypadkw testowych, ktre pokrywaj gwne funkcjonalnoci moduu lub systemu, majcy na celu potwierdzenie, e kluczowe funkcjonalnoci programu dziaaj, bez zagbiania si w szczegy. Codzienne budowanie i testy dymne stanowi dobre praktyki wytwarzania oprogramowania. "},
    { a: "testalia", q: "Wszystkie dokumenty i narzdzia (artefakty) wytworzone i uywane podczas procesu testowania niezbdne do planowania, projektowania i wykonywania testw, takie jak dokumentacja, skrypty, wejcia, oczekiwane rezultaty, procedury, pliki, bazy danych, rodowiska oraz kade dodatkowe oprogramowanie i narzdzia uyte podczas testowania."},
    { a: "tester", q: "Wykwalifikowany profesjonalista, zaangaowany w testowanie moduu lub systemu."},
    { a: "testowanie", q: "Proces skadajcy si z wszystkich czynnoci cyklu ycia, zarwno statycznych jak i dynamicznych. Skoncentrowany na planowaniu, przygotowaniu i ewaluacji oprogramowania oraz powizanych produktw w celu okrelenia czy speniaj one wyspecyfikowane wymagania oraz wykazania, e s one dopasowane do swoich celw oraz do wykrywania usterek."},
    { a: "testowanie ad hoc", q: "Testy wykonywane nieformalnie; nie ma miejsca adne formalne przygotowanie testu, nie jest uyta adna rozpoznawalna technika projektowania przypadkw testowych, brak jest oczekiwa co do rezultatw, wykonaniem testu kieruje dowolno."},
    { a: "testowanie akceptacyjne", q: "Testowanie formalne przeprowadzane w celu umoliwienia uytkownikowi, klientowi lub innemu uprawnionemu podmiotowi ustalenia, czy zaakceptowa system lub modu."},
    { a: "testowanie alfa", q: "Symulowane lub rzeczywiste testy produkcyjne przeprowadzane przez potencjalnych uytkownikw lub niezaleny zesp testowy, przeprowadzane u producenta, ale bez udziau wytwrcw oprogramowania. Testowanie alfa jest czsto wykorzystywane jako forma wewntrznych testw akceptacyjnych dla oprogramowania z pki."},
    { a: "testowanie beta", q: "Testowanie produkcyjne wykonywane przez potencjalnego i/lub istniejcego uytkownika/klienta w zewntrznym miejscu niezwizanym z programistami/twrcami - poza organizacj wytwrcz, w celu podjcia decyzji, czy modu albo system zaspokaja potrzeby uytkownika/klienta i wspgra z procesami biznesowymi. Testowanie beta jest czsto traktowane jako forma zewntrznych testw akceptacyjnych oprogramowania \"z pki\" w celu uzyskania informacji zwrotnej z rynku."},
    { a: "testowanie bezpieczestwa", q: "Testowanie majce na celu okrelenie bezpieczestwa oprogramowania."},
    { a: "testowanie biaoskrzynkowe", q: "Testowanie oparte na analizie wewntrznej struktury moduu lub systemu."},
    { a: "testowanie czarnoskrzynkowe", q: "Testowanie funkcjonalne lub niefunkcjonalne, bez odniesienia do wewntrznej struktury moduu lub systemu."},
    { a: "testowanie decyzji", q: "Biaoskrzynkowa technika projektowania przypadkw testowych polegajca na testowaniu wynikw decyzji."},
    { a: "testowanie dokumentacji", q: "Kontrola jakoci (dokadnoci, prawidowoci, kompletnoci itp.) dokumentacji, np. podrcznika uytkownika lub opisu instalacji."},
    { a: "testowanie dynamiczne", q: "Testowanie, podczas ktrego wykonywany jest kod moduu lub systemu."},
    { a: "testowanie efektywnoci", q: "Testowanie majce na celu okrelenie efektywnoci oprogramowania."},
    { a: "testowanie eksploracyjne", q: "Nieformalna technika projektowania testw, w ktrej tester projektuje testy w czasie, gdy s one wykonywane i wykorzystuje informacje zdobyte podczas testowania do projektowania nowych i lepszych testw. "},
    { a: "testowanie funkcjonalne", q: "Testowanie oparte na analizie specyfikacji funkcjonalnej moduu lub systemu."},
    { a: "testowanie funkcjonalnoci", q: "Testowanie majce na celu okrelenie funkcjonalnoci oprogramowania."},
    { a: "testowanie gruntowne", q: "Podejcie do testw, w ktrym zestaw testowy obejmuje wszystkie kombinacje wartoci wejciowych i warunkw wstpnych."},
    { a: "testowanie integracyjne", q: "Testowanie wykonywane w celu wykrycia defektw w interfejsach i interakcjach pomidzy moduami lub systemami. "},
    { a: "testowanie interfejsu", q: "Testowanie wykonywane w celu wykrycia bdw w interfejsach pomidzy moduami."},
    { a: "testowanie losowe", q: "Czarnoskrzynkowa technika projektowania przypadkw testowych, w ktrej przypadki testowe s dobierane na podstawie pseudolosowego algorytmu dopasowanego do profilu operacyjnego. Technika ta jest wykorzystywana do testw wymaga niefunkcjonalnych, takich jak wydajno lub niezawodno."},
    { a: "testowanie moduowe", q: "Testowanie pojedynczych moduw oprogramowania. "},
    { a: "testowanie niefunkcjonalne", q: "Testowanie atrybutw moduu lub systemu, ktre nie odnosz si do jego funkcjonalnoci, np. niezawodnoci, efektywnoci, pielgnowalnoci i przenaszalnoci."},
    { a: "testowanie niezawodnoci", q: "Proces testowania majcy na celu okrelenie niezawodnoci oprogramowania."},
    { a: "testowanie obcienia", q: "Testowanie, podczas ktrego system pracuje przy duej iloci danych."},
    { a: "testowanie obcieniowe", q: "Rodzaj testw, ktrych zadaniem jest pomiar zachowania moduu lub systemu przy zwikszajcym si obcieniu, np. liczbie rwnolegle pracujcych uytkownikw i/lub liczbie transakcji, w celu okrelenia, jakie obcienie modu lub system jest w stanie obsuy. "},
    { a: "testowanie oparte na atakach", q: "Technika testowania oparta na dowiadczeniu, ktra uywa atakw usterek do wywoywania awarii, szczeglnie awarii zabezpiecze. "},
    { a: "testowanie oparte na ryzyku", q: "Testowanie nastawione na wykrycie i dostarczenie informacji o ryzykach produktowych."},
    { a: "testowanie oparte na wymaganiach", q: "Podejcie do testw, w ktrym przypadki testowe s projektowane w oparciu o cele testw i warunki testowe zawarte w wymaganiach np. testy sprawdzajce konkretne funkcje lub badajce niefunkcjonalne wymagania systemu, takie jak niezawodno lub uyteczno."},
    { a: "testowanie pielgnacyjne", q: "Testowanie zmian we wdroonym systemie lub testowanie wpywu zmienionego rodowiska na wdroony system."},
    { a: "testowanie produkcyjne", q: "Testowanie majce na celu ocen moduu lub systemu w jego rodowisku produkcyjnym. "},
    { a: "Testowanie przeciajce", q: "Typ testowania wydajnociowego wykonywany, by okreli jak system lub jego modu pracuje na przewidywanej lub wyspecyfikowanej granicy lub poza ni lub te przy ograniczonym dostpie do pamici lub serwera."},
    { a: "testowanie przej pomidzy stanami", q: "Czarnoskrzynkowa technika projektowania przypadkw testowych, w ktrej przypadki s projektowane tak, aby sprawdzay dozwolone oraz niedozwolone przejcia midzy stanami. Patrz take testowanie N-przecze."},
    { a: "testowanie przenaszalnoci", q: "Proces testowania majcy na celu okrelenie przenaszalnoci oprogramowania."},
    { a: "testowanie przepywu danych", q: "Biaoskrzynkowa metoda projektowania przypadkw testowych, w ktrej testy projektowane s w oparciu o analiz par definicja - uycie zmiennych."},
    { a: "testowanie przez uytkownika", q: "Testowanie, w ktrym w ocen uytecznoci moduu lub systemu s zaangaowani rzeczywici uytkownicy."},
    { a: "testowanie regresywne", q: "Ponowne przetestowanie uprzednio testowanego programu po dokonaniu w nim modyfikacji, w celu upewnienia si, e w wyniku zmian nie powstay nowe defekty lub nie ujawniy si defekty w niezmienionej czci oprogramowania. Testy takie s przeprowadzane po zmianach oprogramowania lub jego rodowiska pracy."},
    { a: "testowanie statyczne", q: "Testowanie moduu lub systemu na poziomie specyfikacji lub implementacji bez wykonywania tego oprogramowania, np. przegldy lub analiza statyczna kodu."},
    { a: "testowanie strukturalne", q: "Patrz testowanie biaoskrzynkowe"},
    { a: "testowanie systemowe", q: "Proces testowania zintegrowanego systemu w celu sprawdzenia jego zgodnoci z wyspecyfikowanymi wymaganiami."},
    { a: "testowanie uytecznoci", q: "Testowanie majce na celu okrelenie, w jakim stopniu oprogramowanie jest zrozumiae, atwe do nauczenia, atwe w uyciu oraz atrakcyjne dla uytkownikw w okrelonych warunkach. "},
    { a: "testowanie w oparciu o przypadki uycia", q: "Czarnoskrzynkowa technika projektowania przypadkw testowych, w ktrej przypadki testowe s projektowane w ten sposb, by wykonywane byy scenariusze uycia."},
    { a: "testowanie w oparciu o tablic decyzyjn", q: "Czarnoskrzynkowa technika projektowania przypadkw testowych polegajca na sprawdzeniu dziaania moduu lub systemu w reakcji na kombinacje warunkw wejciowych podanych w tablicy decyzyjnej."},
    { a: "testowanie wartoci brzegowych", q: "Patrz analiza wartoci brzegowych"},
    { a: "testowanie wartoci negatywnych", q: "Testowanie polegajce na prbach podawania wartoci, ktre powinny by odrzucane przez modu lub system."},
    { a: "testowanie warunkw", q: "Biaoskrzynkowa technika projektowania przypadkw testowych polegajca na testowaniu wyniku dla warunkw."},
    { a: "testowanie wspdziaania", q: "Proces testowania w celu okrelenia wspdziaania oprogramowania."},
    { a: "testowanie wydajnociowe", q: "Proces testowania majcy na celu okrelenie wydajnoci oprogramowania. Patrz take testowanie efektywnoci."},
    { a: "testowanie zabezpiecze", q: "Testowanie majce na celu okrelenie zabezpiecze oprogramowania."},
    { a: "testowanie zgodnoci", q: "Proces testowania okrelajcy zgodno moduu albo systemu."},
    { a: "typ ryzyka", q: "Specyficzna kategoria ryzyka, zwizana z typem testw, ktre naley przeprowadzi w taki sposb, aby wiadomie je ogranicza i agodzi, np. ryzyko zwizane z interfejsem uytkownika moe by agodzone poprzez przeprowadzenie testw uytecznoci."},
    { a: "typ testw", q: "Grupa czynnoci testowych nakierowanych na testowanie moduu komponentu lub systemu, skoncentrowanych na specyficznych konkretnych celach, takich jak test funkcjonalny, test uytecznoci, test regresywny itp. Typ testw moe by uyty na jednym lub na kilku poziomach testw."},
    { a: "usterka", q: "Patrz defekt."},
    { a: "utrzymanie", q: "Zmiany oprogramowania po wdroeniu produkcyjnym dokonywane w celu naprawy usterek, poprawy wydajnoci lub innych atrybutw oprogramowania. Zmiany mog dotyczy take przystosowania produktu do zmienionego rodowiska. "},
    { a: "uyteczno", q: "Zdolno oprogramowania do bycia uywanym, zrozumiaym, atwym w nauce i atrakcyjnym dla uytkownika, gdy oprogramowanie to jest uywane w okrelonych warunkach."},
    { a: "walidacja", q: "Sprawdzanie poprawnoci i dostarczenie obiektywnego dowodu, e produkt procesu wytwarzania oprogramowania spenia potrzeby i wymagania uytkownika."},
    { a: "warto brzegowa", q: "Warto wejcia lub wyjcia, ktra jest na granicy klas rwnowanoci lub jest w najbliszym przyrostowym ssiedztwie tej granicy. Na przykad warto minimalna lub maksymalna zakresu."},
    { a: "warunek testowy", q: "Element lub zdarzenie moduu lub systemu, ktry moe by zweryfikowany przez jeden lub wicej przypadkw testowych, np. funkcja, transakcja, cecha, atrybut jakoci lub element struktury."},
    { a: "wano", q: "Stopie wpywu defektu na rozwj lub dziaanie moduu lub systemu."},
    { a: "weryfikacja", q: "Egzaminowanie poprawnoci i dostarczenie obiektywnego dowodu, e produkt procesu wytwarzania oprogramowania spenia zdefiniowane wymagania."},
    { a: "wspczynnik awarii", q: "Stosunek liczby awarii w danej kategorii do okrelonej jednostki miary, np. awarie na jednostk czasu, awarie na liczb transakcji, awarie na liczb uruchomie komputera."},
    { a: "wydajno", q: "Stopie, w jaki system lub modu, realizuje swoje wyznaczone funkcje w zaoonych ramach czasu przetwarzania i przepustowoci."},
    { a: "wykonanie testu", q: "Proces przeprowadzenia testu na module lub systemie, w wyniku ktrego otrzymujemy rzeczywiste rezultaty."},
    { a: "wymaganie funkcjonalne", q: "Wymaganie specyfikujce funkcj, ktr modu lub system musi realizowa."},
    { a: "wymaganie niefunkcjonalne", q: "Wymaganie, ktre nie dotyczy funkcjonalnoci, ale cech oprogramowania takich, jak niezawodno, efektywno, uyteczno, pielgnowalno i przenaszalno"},
    { a: "wymaganie testowalne", q: "Wymaganie, ktre jest okrelone przy uyciu poj umoliwiajcych stworzenie projektu testu (a nastpnie przypadkw testowych) i wykonanie testw w celu okrelenia, czy wymaganie to zostao spenione."},
    { a: "wytwarzanie sterowane testami", q: "Sposb wytwarzania oprogramowania, w ktrym przypadki testowe s przygotowywane i czsto automatyzowane zanim powstanie oprogramowanie, ktre bdzie testowane za ich pomoc. Powszechnie stosowany skrt to TDD  od ang. test driven development."},
    { a: "zabezpieczenie", q: "Atrybuty oprogramowania okrelajce jego zdolno do zapobiegania nieautoryzowanym przypadkowym lub umylnym dostpem do programu i do danych."},
    { a: "zablokowany przypadek testowy", q: "Przypadek testowy, ktry nie moe zosta wykonany, poniewa jego warunki wstpne nie mog zosta osignite."},
    { a: "zaliczenie", q: "Test jest uwaany za zaliczony, jeli jego rezultat pasuje do rezultatu oczekiwanego."},
    { a: "zarzdzanie danymi testowymi", q: "Proces analizy wymaga dla danych testowych, projektowania struktur danych testowych, tworzenia i utrzymywania danych testowych."},
    { a: "zarzdzanie defektami", q: "Proces skadajcy si z rozpoznania, analizy, prowadzenia dziaa i likwidacji usterek. Polega on na rejestracji usterek, ich klasyfikacji oraz okrelaniu wpywu defektw."},
    { a: "zarzdzanie incydentami", q: "Proces skadajcy si z rozpoznania, analizy, podejmowania dziaa i rozwizywania incydentw. Polega on na rejestracji klasyfikacji oraz okrelaniu wpywu incydentw."},
    { a: "zarzdzanie jakoci", q: "Og skoordynowanych czynnoci majcych na celu kierowanie organizacj i kontrolowanie jej pod ktem jakoci. Zwykle obejmuje czynnoci takie jak: zdefiniowanie polityki jakociowej i celw jakociowych, planowanie jakoci, kontrolowanie jakoci, zapewnienie jakoci i poprawa jakoci."},
    { a: "zarzdzanie konfiguracj", q: "Dyscyplina uywajca technicznych i administracyjnych metod kierowania i nadzoru aby: okreli i udokumentowa charakterystyki funkcjonalne i fizyczne elementw konfiguracji, kontrolowa zmiany tych charakterystyk, zapisywa i raportowa o wykonywaniu zmian i statusie implementacji oraz weryfikowa zgodno z wyspecyfikowanymi wymaganiami."},
    { a: "zalepka", q: "Szkieletowa albo specjalna implementacja moduu uywana podczas produkcji lub testw innego moduu, ktry t zalepk wywouje albo jest w inny sposb od niej zaleny. Zalepka zastpuje wywoywany modu."},
    { a: "zestaw testowy", q: "Cig przypadkw testowych, w ktrym warunki wyjciowe z jednego testu uywa si jako warunki wejciowe do nastpnego testu."},
    { a: "zgadywanie bdw", q: "Technika projektowania testw, gdzie bazujc na dowiadczeniu testera przewiduje si, jakie defekty, bdce efektem wykonanych pomyek, mog by obecne w testowanym module lub systemie i projektuje si testy tak, aby te defekty ujawni."},
    { a: "zgoszenie defektu", q: "Dokument opisujcy usterk w module lub systemie, ktra moe spowodowa nieprawidowe dziaanie jego wymaganych funkcji."},
    { a: "zoono", q: "Stopie trudnoci do nauczenia, utrzymania i weryfikacji, wedug ktrego modu lub system i/lub jego wewntrzna struktura zosta (zostaa) zaprojektowany(a)."},
    { a: "zmiana stanu", q: "Przejcie pomidzy dwoma stanami systemu lub moduu."},
    { a: "zmienna", q: "Element pamici komputera, ktry jest dostpny w programie poprzez swoja nazw."},
    { a: "zwinne wytwarzanie oprogramowania", q: "Grupa metodyk wytwarzania oprogramowania oparta na iteracyjnym, przyrostowym modelu wytwarzania oprogramowania, w ktrych wymagania i rozwizania ewoluuj poprzez wspprac w ramach samoorganizujcych si, realizujcych wiele funkcji zespow."},
    ]
}