import React from "react";
import styled from 'styled-components';
import { Heading } from "@chakra-ui/core";

const ColoredHeaderStyled = styled(Heading)`
  background-color: ${props => props.ansColor};
  height: 250px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid;
  margin-bottom: 10px;
`;

const renderers = {
  string: ({ question: { value } }) => <Heading>{
    Array.isArray(value) ? value.join(", ") : value
  }</Heading>,
  color: ({ question: { value }, answer: { value: ansValue }, questionConfig }) => {
    const color = questionConfig && questionConfig.color || ansValue
    return (
      <ColoredHeaderStyled ansColor={color}>
        <span style={{ color: ansValue === "gray" ? 'black' : color, filter: "invert(100%) contrast(10)" }}>{value}</span>
      </ColoredHeaderStyled>
    )
  },
};

const QuestionRenderer = props => {
  const TypeRenderer = renderers[props.question.type];
  return <TypeRenderer {...props} />;
};

export default QuestionRenderer;
