import styled from "styled-components";

export const InputStyled = styled.input`
  padding: 10px;
  border: 0;
  outline: 0;
  font-size: 200%;
  text-align: center;
  border-bottom: 1px solid #999;
  margin-top: 10px;
  ${props => props.shouldApplyMask ? 'letter-spacing: 5px' : ''}

  &:focus {
    border-bottom: 1px dashed #999;
    outline: 0;
    background-color: ivory;
  }
`;

export const Hints = styled.aside`
  margin-top: 40px;

  & > * + * {
    margin-left: 10px;
  }
`;

export const OverlayStyled = styled.div`
  display: ${props => props.show ? "block" : "none"};
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #0008;
`;

export const TopBarStyled = styled.nav`
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 30px;
  background-color: #e0e0e0;
`;
export const TopBarPlaceholder = styled.div`
  width: 100%;
  display: block;
  height: 30px;
  margin-bottom: 20px;
`;
