const storage = localStorage;

export const store = (key, data) => localStorage.setItem(key, JSON.stringify(data));

export const retrieve = key => {
  try {
    return JSON.parse(localStorage[key]);
  } catch (e) {
    console.log(e);
  }
};

const getKey = ({ currentDataset, config={}, currentDatasetId }) =>
  `${currentDataset ? currentDataset.id : currentDatasetId}-${config.chunkCount}-${config.chunkId}`;

// L A S T   C O N F I G
const CONFIG_KEY = 'config';
export const saveConfig = ({ config, currentDataset }) =>
  store(CONFIG_KEY, {
    config,
    currentDatasetId: currentDataset.id,
  });

export const loadConfig = () => retrieve(CONFIG_KEY);

// P R O G R E S S
export const saveProgress = state => {
  const key = getKey(state);
  store(key, {
    currentIdx: state.currentIdx,
    done: state.done,
    anyWasIncorrect: state.anyWasIncorrect,
    memStack: state.memStack,
    list: state.list,
  });
};

export const loadProgress = state => state ? retrieve(getKey(state)) : undefined;

export const clearProgress = state =>
  storage.removeItem(getKey(state));

// W H O L E   S T A T E
export const saveState = state => {
  saveProgress(state);
  saveConfig(state);
}
export const loadState = () => {
  const config = loadConfig();
  const progress = loadProgress(config);

  return config && progress && {
    ...config,
    ...progress,
  };
}
export const hasState = state =>
  !!storage[getKey(state)];

// N O T E S
const NOTES_KEY = "notesKey";
export const saveNotes = notes => storage.setItem(NOTES_KEY, notes);
export const retrieveNotes = () => storage.getItem(NOTES_KEY);
