const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition
const SpeechGrammarList = window.SpeechGrammarList || window.webkitSpeechGrammarList
// const SpeechRecognitionEvent = window.SpeechRecognitionEvent || window.webkitSpeechRecognitionEvent

const tmpWordList = 'condition|above|agency|after|ago|act|along|adult|across|administration|ability|agreement|civil|admit|check|clearly|church|contain|against|clear|compare|color|citizen|congress|consider|child|conference|come|choose|age|class|close|city|affect|all|common|cold|company|allow|coach|choice|computer|community|action|continue|accept|air|add|alone|actually|agree|almost|commercial|ahead|about|activity|able|according|claim|college|concern|account|collection|again|address|agent|consumer'.split("|");

let speechRecognitionList;
const updateGrammar = (wordList = tmpWordList) => {
  const grammar = `#JSGF V1.0; grammar colors; public <color> = ${wordList.join("|")};`;
  speechRecognitionList = new SpeechGrammarList();
  speechRecognitionList.addFromString(grammar, 1);
}

const recognition = SpeechRecognition ? new SpeechRecognition() : {};
recognition.lang = 'en-US';
recognition.interimResults = true;
recognition.maxAlternatives = 1;
recognition.continuous = true;

// recognition.onresult = evt => {
//   const last = evt.results.length - 1;
//   const word = evt.results[last][0].transcript;
// diagnostic.textContent = 'Result received: ' + color + '.';
// bg.style.backgroundColor = color;
//   console.log('Word:', word, 'Confidence: ' + evt.results[0][0].confidence);
// }

recognition.onstart = () => {
  console.log('Voice recognition is ON.');
}

recognition.onspeechend = () => {
  recognition.stop();
  console.log('Speech has stopped being detected');
}

recognition.onnomatch = evt => {
  console.log('I didnt recognise that color.');
}

// recognition.onerror = evt => {
//   if(event.error === 'no-speech') {
//     console.log('Try again.');
//   }
// }

window.r = recognition;

export {
  recognition,
  updateGrammar,
};
