import React from "react";
import PropTypes from "prop-types";

import { Button } from "@chakra-ui/core";

import { recognition, updateGrammar } from "components/SpeachRecognition";
import SpeachSynthesis from "components/SpeachSynthesis";
import QuestionRenderer from "components/Renderers/QuestionRenderer";
import { InputStyled, Hints } from "./stylesComponents";

function setCaretPosition(ctrl, pos) {
  pos = pos || ctrl.value.length;
  // Modern browsers
  if (ctrl.setSelectionRange) {
    ctrl.focus();
    ctrl.setSelectionRange(pos, pos);

    // IE8 and below
  } else if (ctrl.createTextRange) {
    var range = ctrl.createTextRange();
    range.collapse(true);
    range.moveEnd('character', pos);
    range.moveStart('character', pos);
    range.select();
  }
}

const Renderer = ({
  answer,
  config,
  handleChange,
  handleKeyUp,
  hintConfig,
  isDone,
  maxLength,
  question,
  questionConfig,
  shouldApplyMask,
  shouldListen = false,
  shouldSpeak = true,
  specialChars,
  value,
  voiceURI,
}) => {
  const [isListening, setIsListening] = React.useState(false);
  const inputRef = React.useRef(null)
  const focusSetter = ({ key }) => key === " " && inputRef.current.focus();
  React.useEffect(() => {
    // inputRef.current && typeof inputRef.current.focus === "function" && inputRef.current.focus();
    document.addEventListener('keypress', focusSetter)

    return () => { document.removeEventListener('keypress', focusSetter) }
  });

  const Input = shouldApplyMask ? MaskedInput : InputStyled;

  recognition.onresult = evt => handleKeyUp(evt, true, recognition);

  const onSpecialKey = evt => handleKeyUp({target: { value: value + evt.target.getAttribute('data-char') }});

  return (
    <div
      style={{
        width: "100%",
        height: "100%",
        display: "flex",
        textAlign: "center",
        alignItems: "center",
        justifyContent: "center",
        padding: "0 20px",
      }}
    >
      <div style={{ display: "block", opacity: isDone ? .5 : 1 }}>
        <QuestionRenderer question={question} answer={answer} questionConfig={questionConfig} />
        <SpeachSynthesis text={answer.value} voiceURI={voiceURI}>
          {({ speak }) => (
            <Button
              size="sm"
              disabled={isListening || isDone}
              variantColor="teal"
              onClick={speak}
            >
              Play
            </Button>
          )}
        </SpeachSynthesis>{" "}
        <Button
          disabled={isDone}
          size="sm"
          variantColor="teal"
          onClick={() => {
            setIsListening(!isListening);
            recognition[isListening ? "abort" : "start"]();
          }}
        >
          {isListening ? "Stop" : "Listen"}
        </Button>
        <br />
        <InputStyled
          autoFocus={true}
          disabled={isDone}
          maxLength={maxLength}
          onChange={handleChange}
          onKeyUp={handleKeyUp}
          ref={inputRef}
          shouldApplyMask={shouldApplyMask}
          value={value}
        />
        <br />
        {specialChars && specialChars.map(char => (
          <Button variantColor="teal" variant="outline" onClick={onSpecialKey} key={char} data-char={char}>{char}</Button>
        ))}
        {hintConfig && (
          <Hints>
            {hintConfig.map(({ label, disabled, callback, variantColor }) => (
              <Button
                key={label}
                disabled={disabled || isDone}
                variantColor={variantColor || "teal"}
                variant="outline"
                onClick={callback}
                title={disabled ? "To be implemented" : ""}
              >
                {label}
              </Button>
            ))}
          </Hints>
        )}
      </div>
    </div>
  );
};

Renderer.propTypes = {
  answer: PropTypes.object.isRequired,
  shouldApplyMask: PropTypes.bool,
  question: PropTypes.object.isRequired,
  value: PropTypes.string.isRequired,
  shouldListen: PropTypes.bool,
  shouldSpeak: PropTypes.bool,
  gameTypeConfig: PropTypes.object,
  hintConfig: PropTypes.array,
};

export default Renderer;
