import React from "react";
import PropTypes from "prop-types";

import Renderer from "./Renderer";
import ger1000 from "~/assets/ger1000";
import eng1000 from "~/assets/eng1000";
import eng1000at from "~/assets/eng1000at";
import slownikTestera from "~/assets/slownikTestera";
import colors from "~/assets/colors";
import AnswerArchive from "./AnswerArchive";
import { saveState, loadState, hasState, saveConfig, loadProgress} from "../../services/storage";
import { speak } from '~/services/speachSynthesis';
import { TopBarStyled, TopBarPlaceholder } from './stylesComponents'
import { Heading, Button } from "@chakra-ui/core";

import {
  convertToQuestionFormat,
  convertToAnswerFormat,
  convertToColorQuestionFormat,
} from "~/utils/converters";

// const areEqual = (a, b) => a.trim() === b.trim();
const areEqualCaseInsensitive = (a, b) => a.trim().toLowerCase() === b.trim().toLowerCase();

const getRecognizedText = evt => {
  const last = evt.results.length - 1;
  return evt.results[last][0].transcript;
};

const getMaxLengthOfAnswer = list => list.reduce(
  (acc, { a: item }) => Math.max(acc, item.length),
  0
);

class GameController extends React.PureComponent {
  initialState = {
    answer: null,
    anyWasIncorrect: false,
    currentIdx: -1,
    done: false,
    hasBeenPlayed: false,
    initiated: false,
    list: [],
    memStack: [],
    question: null,
    shouldApplyMask: false,
    value: "",
    config: {
      playOnNewQuestion: false,
      playOnQuestionOnAnswer: false,
      acceptOnCorrect: true,
      chunkCount: 1,
      chunkId: 1,
    }
  };
  state = this.initialState;
  voices = []; // placeholder for speechSynthesis voices
    
  componentDidMount = () => {
    this.voices = window.speechSynthesis.getVoices();
    window.speechSynthesis.onvoiceschanged = () => {
      this.voices = window.speechSynthesis.getVoices();
      this.forceUpdate();
    };

    const fromLocalStorage = loadState();

    if (fromLocalStorage) {
      this.loadStateFromLS(fromLocalStorage);
    } else {
      this.setState(this.initialState, () => {
        this.setupDataset({ idx: 0 });
      })
    }
  }

  loadStateFromLS = fromLocalStorage => {
    this.setState({
      ...fromLocalStorage,
      currentIdx: fromLocalStorage.currentIdx - 1,
      currentDataset:
        this.props.datasets.find(({ id }) => id === fromLocalStorage.currentDatasetId),
    }, () => this.loadNextQuestion(fromLocalStorage.anyWasIncorrect));
  }

  loadProgressFromLS = () => {
    const progress = loadProgress(this.state);
    this.setState({
      ...progress,
      currentIdx: progress.currentIdx - 1,
    }, () => this.loadNextQuestion(progress.anyWasIncorrect));
  }

  setupDataset = ({
    idx = this.state.currentDatasetIdx || 0,
    chunkCount = this.state.config.chunkCount || 1,
    chunkId = this.state.config.chunkId || 1,
  }) => {
    const currentDataset = this.props.datasets[idx];
    const wholeList = currentDataset.data.list;
    const listLen = currentDataset.data.list.length;

    const _chunkCount = Math.max(chunkCount, 1);
    const _chunkSize = Math.ceil(listLen / _chunkCount);
    const _chunkId = Math.min(Math.max(chunkId, 1), _chunkCount);

    const list = [];
    const startFrom = _chunkSize * (_chunkId - 1);
    const endAt = _chunkId === _chunkCount
      ? listLen
      : _chunkSize * _chunkId;
    
    for(let i = startFrom; i < endAt; i++) {
      list.push( wholeList[i] );
    }

    this.setState(oldState => ({
      ...this.initialState,
      // config: this.state.config,
      initiated: true,
      currentDataset,
      currentDatasetIdx: idx,
      config: {
        ...oldState.config,
        chunkCount: _chunkCount,
        chunkSize: _chunkSize,
        chunkId: _chunkId,
      },
      list,
      memStack: [],
      maxLength: getMaxLengthOfAnswer(list),
    }), () => {
      if (hasState(this.state)) {
        this.loadProgressFromLS();
      } else {
        saveState(this.state);
        this.handleHit();
      }
    })
  }

  handleHit = () => this.pushToMemStack(true);
  giveUp = () => this.pushToMemStack(false);

  pushToMemStack = wasCorrect => {
    this.state.config.playOnQuestionOnAnswer && speak(this.state.answer.value);

    if (this.state.currentIdx >= 0) {
      const prev = this.state.list[ this.state.currentIdx ];
      this.state.memStack.unshift({
        item: prev,
        wasCorrect,
        value: this.state.value,
      });
    }

    if (this.state.currentIdx + 1 >= this.state.list.length) {
      this.setState({
        done: true,
        anyWasIncorrect: this.state.anyWasIncorrect || !wasCorrect
      });
      return;
    }

    this.loadNextQuestion(wasCorrect);
  };

  loadNextQuestion = wasCorrect => {
    this.setState(({ currentIdx: prevCurrent }) => {
      const currentIdx = prevCurrent + 1;
      const { a: answer, q: question, c: questionConfig } = this.state.list[currentIdx];
      const convertedAnswer = this.state.currentDataset.answerConverter(answer);
      this.state.config.playOnNewQuestion && speak(convertedAnswer.value);
      return {
        answer: convertedAnswer,
        currentIdx,
        question: this.state.currentDataset.questionConverter(question),
        questionConfig,
        shouldApplyMask: false,
        value: "",
        anyWasIncorrect: this.state.anyWasIncorrect || !wasCorrect
      };
    }, () => {
      saveState(this.state);
    });
  }

  handleKeyUp = (evt, fromVoice, recognition) => {
    if (evt.isComposing || evt.keyCode === 229) return;

    let value = fromVoice ? getRecognizedText(evt) : evt.target.value;
    let key = fromVoice ? undefined : evt.key;

    if (key === 'Escape') {
      return this.giveUp();
    } else if (key === 'Enter') {
      this.pushToMemStack(value && areEqualCaseInsensitive(value, this.state.answer.value));
    } else if(this.state.config.acceptOnCorrect &&
      value && areEqualCaseInsensitive(value, this.state.answer.value)
    ) {
      this.handleHit(true);
    // } else if (key && key.startsWith('Arrow')) {
    //   return;
    } else {
      this.setState({ value });
    }
  };

  handleChange = this.handleKeyUp;

  showFirstLetter = () => {
    this.setState({
      value: this.state.answer.value.charAt(0),
    });
  };
  applyMask = () => this.setState({ shouldApplyMask: true });
  checkSpelling = () => {};
  reload = () => this.setupDataset({ idx: this.state.currentDatasetIdx });

  continueWithIncorrect = () => {
    this.setState(
      {
        list: this.state.memStack.reduce(
          (acc, { value, wasCorrect }) =>
            !wasCorrect ? acc.concat(value) : acc,
          []
        ),
        done: false,
        currentIdx: -1,
        memStack: this.state.memStack.filter(({ wasCorrect }) => wasCorrect),
        anyWasIncorrect: false
      },
      this.handleHit
    );
  };

  onChangeConfig = evt => {
    const { id, value } = evt && evt.target;
    this.setState(oldState => ({
      config: {
        ...oldState.config,
        [id]: value,
      }
    }));
  }

  onVoiceChange = evt => this.setState({ currentVoice: evt.target.value });

  onDatasetChange = ({ target: { value } }) =>
    this.setupDataset({ idx: value });

  render = () => this.state.question && (
    <>
      <TopBarStyled>
      <p align="right">
        <select style={{ margin: '5px 20px 0' }} onChange={this.onVoiceChange} value={this.state.currentVoice} id="voicePicker">
          {(this.voices||[]).map(({ lang, name, voiceURI, localService }) => (
            <option value={voiceURI} key={voiceURI} title={name}>{
              `${name.length > 27 ? name.substr(0, 27)+'...' : name } (${lang})`
            }</option>
          ))}
        </select>
        <input
          type="checkbox"
          checked={this.state.config.acceptOnCorrect}
          onChange={this.onChangeConfig}
          checked={this.state.config.acceptOnCorrect}
          value={this.state.config.acceptOnCorrect}
          id="acceptOnCorrect"
        /> <label htmlFor="acceptOnCorrect">acceptWhenCorrect</label>{" "}
        <input
          type="checkbox"
          checked={this.state.config.playOnQuestionOnAnswer}
          onChange={this.onChangeConfig}
          checked={this.state.config.playOnQuestionOnAnswer}
          value={this.state.config.playOnQuestionOnAnswer}
          id="playOnQuestionOnAnswer"
        /> <label htmlFor="playOnQuestionOnAnswer">playOnSubmit</label>{" "}
        <input
          type="checkbox"
          onChange={this.onChangeConfig}
          checked={this.state.config.playOnNewQuestion}
          value={this.state.config.playOnNewQuestion}
          id="playOnNewQuestion"
        /> <label htmlFor="playOnNewQuestion">playOnNewQuestion</label>

        <select style={{ margin: '5px 20px 0' }} onChange={this.onDatasetChange} value={this.state.currentDatasetIdx} id="datasetPicker">
          {this.props.datasets.map(({ label }, idx) => (
            <option value={idx} key={label}>{label}</option>
          ))}
        </select>
        
        <input id="chunkCount" type="number" maxLength="1" size={1} min={1} max={50} value={this.state.config.chunkCount}
          onChange={this.onChangeConfig}
          onBlur={evt => {
            this.setupDataset({
              chunkCount: +evt.target.value,
            });
          }}
        /> <label htmlFor="chunkCount">ChunkCount</label>

        <input id="chunkId" type="number" maxLength="1" size={1} min={1} max={this.state.config.chunkCount}
          value={Math.min(this.state.config.chunkId, this.state.config.chunkCount)}
          onChange={this.onChangeConfig}
          onBlur={evt => {
            this.setupDataset({
              chunkCount: this.state.config.chunkCount,
              chunkId: +evt.target.value,
            });
          }}
        /> <label htmlFor="chunkId">ChunkId</label>
      </p>
      </TopBarStyled>
      <TopBarPlaceholder />
      {this.state.done && (
        <div style={{ textAlign: "center" }}>
          <Heading
            style={{
              display: "inline",
              lineHeight: "80px",
              marginRight: "20px"
            }}
          >
            Done!
          </Heading>
          <Button
            style={{ verticalAlign: "super" }}
            onClick={this.reload}
            variantColor="teal"
            size="md"
          >
            Reload
          </Button>{" "}
          <Button
            style={{ verticalAlign: "super" }}
            onClick={this.continueWithIncorrect}
            variantColor="teal"
            size="md"
            disabled={!this.state.anyWasIncorrect}
          >
            Go through incorrect
          </Button>
        </div>
      )}
      <Renderer
        specialChars={this.state.currentDataset.data.specialChars}
        shouldApplyMask={this.state.shouldApplyMask}
        voiceURI={this.state.currentVoice}
        playOnNewQuestion={this.state.playOnNewQuestion}
        question={this.state.question}
        color={this.state.color}
        answer={this.state.answer}
        questionConfig={this.state.questionConfig}
        handleKeyUp={this.handleKeyUp}
        handleChange={this.handleChange}
        value={this.state.value}
        shouldApplyMask={this.state.shouldApplyMask}
        isDone={this.state.done}
        hintConfig={[
          {
            label: "First Letter",
            disabled: false,
            callback: this.showFirstLetter
          },
          {
            label: "Apply Mask",
            disabled: true,
            callback: this.applyMask
          },
          {
            label: "Check Spelling",
            disabled: true,
            callback: this.checkSpelling
          },
          {
            label: "Give up",
            disabled: false,
            callback: this.giveUp,
            variantColor: "red"
          }
        ]}
      />
      <AnswerArchive archive={this.state.memStack} />
    </>
  );
}

GameController.propTypes = {
  datasets: PropTypes.arrayOf(PropTypes.object),
}

GameController.defaultProps = {
  datasets: [
    {
      id: "eng1000at",
      label: "1000 Words Ania",
      data: eng1000at,
      answerConverter: convertToAnswerFormat,
      questionConverter: convertToQuestionFormat,
    },
    {
      id: "eng1000",
      label: "1000 Words English",
      data: eng1000,
      answerConverter: convertToAnswerFormat,
      questionConverter: convertToQuestionFormat,
    },
    {
      id: "slownikTestera",
      label: "Slownik Testera",
      data: slownikTestera,
      answerConverter: convertToAnswerFormat,
      questionConverter: convertToQuestionFormat,
    },
    {
      id: "colors1",
      label: "English Colors",
      data: colors,
      answerConverter: convertToAnswerFormat,
      questionConverter: convertToColorQuestionFormat,
    },
    {
      id: "ger1000",
      label: "1000 Words German",
      data: ger1000,
      answerConverter: convertToAnswerFormat,
      questionConverter: convertToQuestionFormat,
    },
  ]
};

export default GameController;
