export const convertToQuestionFormat = question => ({
  type: "string",
  value: question,
});

export const convertToAnswerFormat = answer => ({
  type: "string",
  value: answer,
});

export const convertToColorQuestionFormat = question => ({
  type: "color",
  value: question,
});






