import React from "react";
import PropTypes from "prop-types";

const config = { voice: false }; // placeholder, TODO: provide global config with voice selections

class SpeachSynthesis extends React.PureComponent {
  constructor(props) {
    super(props);
    this.synth = window.speechSynthesis;
    this.utterThis = new window.SpeechSynthesisUtterance("");
    this.voices = this.synth.getVoices();
    this.defaultVoice =
      this.voices[0] && this.voices[0].default
        ? this.voices[0]
        : this.voices.filter(({ 'default': isDefault }) => isDefault);
  }

  cancel = () => this.synth.cancel;
  pause = () => this.synth.pause;
  resume = () => this.synth.resume;
  speak = () => this.synth.speak;

  speak = () => {
    const { text, pitch, rate, voice, volume } = this.props;
    this.utterThis.text = text;
    // this.utterThis.voice = voice || config.voice || this.defaultVoice || null;
    this,this.utterThis.voice = this.synth.getVoices().find(({ voiceURI }) => voiceURI === this.props.voiceURI)
    // this.utterThis.volume = volume;
    // this.utterThis.rate = rate;
    // this.utterThis.pitch = pitch;

    this.synth.speak(this.utterThis);
  };

  render = () =>
    this.props.children({
      speak: this.speak,
      cancel: this.cancel,
      pause: this.pause,
      resume: this.resume,
      speak: this.speak,
    });
}

SpeachSynthesis.propTypes = {
  text: PropTypes.string.isRequired,
  pitch: PropTypes.number,
  rate: PropTypes.number,
  voice: PropTypes.object,
  volume: PropTypes.number
};

export default SpeachSynthesis;
