import React from "react";
import styled from "styled-components";
import { PlayArrow } from '@material-ui/icons';

import speak from '../../services/speachSynthesis';

const ListStyled = styled.ul`
  display: inline-block;
  font-size: 18px;
  list-style-type: none;
  margin-top: 20px;
  margin: 0 auto;
  vertical-align: top;
  width: 50%;
`;

const SectionStyled = styled.section`
  margin: 50px auto 0;
  opacity: 0;
  transition: opacity .2s;
  width: 80%;

  &.visible {
    opacity: 1;
  }
`;

const sayMyId = evt => speak(evt.target.id || evt.target.parentNode.id);
const SpeakerStyled = styled(PlayArrow)`cursor: pointer;`;
const ActiveSpeaker = ({ id }) => <SpeakerStyled id={id} onClick={sayMyId} />;

const ArchiveItem = ({ answer, question, color, arr, idx, value }) => (
  <li style={{ color }}>
    <ActiveSpeaker id={answer} />
    {arr.length - idx}. {" "}
    {answer} - {
      Array.isArray(question) ? question.join(", ") : question
    }{value && (
      <>
        &nbsp;- {value}
      </>
    )}
  </li>
)

const AnswerArchive = ({ archive }) => (
  <SectionStyled className={archive && archive.length > 0 ? "visible" : ""}>
    <header>
      <h2>Archive</h2>
    </header>
    <hr style={{margin: "0 0 20px 0"}} />
    <div>
      <ListStyled>
        {archive.length > 0 &&
          archive.filter(({ wasCorrect }) => wasCorrect).map(({ item: { a: answer, q: question }, value }, idx, arr) => (
            <ArchiveItem
              key={idx}
              answer={answer}
              question={question}
              color={'green'}
              arr={arr}
              idx={idx}
            />
          ))}
      </ListStyled>
      <ListStyled>
        {archive.length > 0 &&
          archive.filter(({ wasCorrect }) => !wasCorrect).map(({ item: { a: answer, q: question }, value }, idx, arr) => (
            <ArchiveItem
              key={idx}
              answer={answer}
              question={question}
              color={'red'}
              arr={arr}
              value={value}
              idx={idx}
            />
          ))}
      </ListStyled>
      </div>
      <br clear="both" />
  </SectionStyled>
);
export default AnswerArchive;
